import React, { Component } from 'react'


class MyProject extends React.Component {
	render() {
		return (
			<div className="col-md-6 animate-box" data-animate-effect={this.props.key % 2 ? "fadeInLeft" : "fadeInRight"}>
				<div className="project" style={{ backgroundImage: 'url('.concat(this.props.imgurl).concat(')') }}>
					<a href={this.props.href}>
						<div className="desc">
							<div className="con">
								<h3>{this.props.title}</h3>
								<span>{this.props.content}</span>
								<p className="icon">
								</p>
							</div>
						</div>
					</a>
				</div>
			</div>

		)
	}
}


